
import { computed } from 'vue'
import { useStore } from 'vuex'
import PartialList from '@/components/partials/List.vue'

export default {
  name: 'view-favorites',
  components: {
    PartialList
  },
  setup () {
    const store = useStore()
    const favorites = computed(() => store.state.favorites)
    return {
      favorites
    }
  }
}
